.navBar {
  background-color: #1689d1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  gap: 150px;
  padding-left: 60px;
}


.navBar img {
  height: 80px;
}

.navBar .search-container {
  position: relative;
  width: 500px;
}

.navBar .input-container {
  position: relative;

}

.navBar input {
  background-color: #0267a3;
  color: #fff;
  padding: 10px 40px 10px 20px;
  /* Ajustei o padding para dar espaço ao ícone */
  border: none;
  font-size: 1rem;
  height: 20px;
  width: 100%;
  border-radius: 5px;
  margin-left: -60px;
}

.navBar .search-icon {
  position: absolute;
  right: -45px;
  top: 30%;
  /* Centraliza verticalmente */
  transform: translateY(-50%);
  /* Centraliza verticalmente */
  color: #ffffff69;
  /* Cor do ícone */
  transform: rotate(90deg);
  margin-right: 60px;
}

.navBar input:focus {
  outline: 0;
}

.navBar input::placeholder {
  color: #ffffff69;
  font-size: 1rem;
}