.home {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
}

.home-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 30px;
    width: 100%;
    border-bottom: 1px solid rgb(226, 226, 226);
}

.texto-clima {
    display: flex;
    flex-direction: column;
    width: 470px;
    margin: 15px 0px 0px -20px;
    align-items: baseline
}

.home-container h2 {
    font-size: 2rem;
    margin: 30px 0px 10px 0px;
}

.home-container p {
    font-size: 1.2rem;
    height: 0px;
}

.texto-home {
    display: flex;
    letter-spacing: 1px;
    gap: 20px;
}

.container-temperatura {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.temperatura {
    display: flex;
    margin-bottom: 5px;
    margin-top: -30px;
    gap: 30px;
    align-items: center;
    justify-content: center;
}

.temperatura p {
    line-height: 0px;
    font-size: 1.5rem;
}

.min-max {
    display: flex;
    height: 20px;
    margin-top: 15px;
    gap: 40px;
}

.lista-previsao {
    list-style: none;
    width: 900px;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 0px;
    margin-top: 30px;
    margin-left: -25px;

}

.lista-previsao li {
    padding: 0px 40px;
    width: 360px;
    height: 60px;
    gap: 15px;
    display: flex;
    align-items: center;
}

.img-sensacao-termica {
    width: 40px;
    margin-left: 10px;
}

.img-chuva {
    width: 45px;
    margin: 10px 0px 0px 10px;
}

.img-horario-atualizacao {
    width: 30px;
    margin-left: 17px;
}

.img-umidade {
    width: 70px;
    margin-left: -7px;
}

.img-vento {
    margin-left: -3px;
}

.img-temperatura {
    width: 50px;
    margin-left: 7px;
}

.img-nascer-sol{
    width: 45px;
    margin-left: 10px;
}

.img-por-sol{
    width: 55px;
    margin-left: 5px;
}




.li-1,
.li-2,
.li-3,
.li-4,
.li-5,
.li-6 {
    border-bottom: 1px solid gray;
}

.li-2,
.li-4,
.li-6,
.li-8 {
    border-left: 1px solid gray;
}

.seta {
    height: 30px;
}